
.grouptitle{height: 5.2rem;background-image: url(../assets/images/ic_group_banner.png);background-size:cover;background-repeat: no-repeat;background-position:center;
}
.grouptitle .big{line-height: .77rem;color: #293132;font-size: .56rem;height: .77rem;font-weight: bold;padding-top: 1.9rem;
}
.grouptitle .mid{line-height: .6rem;height: .6rem;color:#c9ced4;font-size: .46rem;
}
.grouptitle .small{height: .5rem;line-height: .5rem;text-align: left;padding-left: .5rem;font-size: .4rem;color: #b0b0b8;padding-top: 1.35rem;
}



