
.movierec{padding-left: .5rem;margin-top: .33rem;background: white;text-align: left;overflow: hidden;
}
.movierec .wrap{width: 100%;height: 9rem;
}
.movierec h3{height: 2.17rem;line-height: 2.17rem;font-size: .56rem;color: #242424;
}
.movierec .title{position: relative;
}
.movierec .more{font-size: .4rem;color: #32b950;height: 2.17rem;line-height: 2.17rem;position: absolute;right: .6rem;top: 0;
}
.movierec .pool{height: 6.56rem;
}
.swiper02 .swiper-slide{width: 4rem;
}



