
.information{width: 100%;height: 4.12rem;background-color: #f79e4c;color: white;text-align: left;letter-spacing: .03rem
}
.information{background-image: url(../assets/images/live_player_bg.jpg);background-size: cover;background-repeat: no-repeat;
}
.information .user_icon{width: 2.35rem;height: 2.35rem;border-radius: 50%;overflow: hidden;margin-left: .65rem;margin-top: .62rem;
}
.information img{width: 100%;height: 100%;
}
.information .info_left{width: 30%;float: left;
}
.information .info_right{float: left;padding-top: .74rem;width: 70%
}
.information .name{font-size: .6rem;line-height: .9rem;
}
.information .name i{display: inline-block;height: .30rem;width: .28rem;padding-left: .2rem
}
.information .id{font-size: .32rem;line-height: .58rem;padding-bottom: .25rem;
}
.information .line{height: 0.03rem;width:100%;background: white;opacity: .2;
}
.information .count{font-size: .4rem;line-height: .64rem;margin-top: .48rem;
}
.information .count span{margin-right: .73rem;
}



